.article-index-card {
  width: 250px;
  margin: 1em 2em;

  &.far-from-top {
    @include respond-to(phone) {display: none;}
  }

  @include respond-to(phone) {
    width: 40%;
    margin: 1em auto;
  }

  .article-index-card-category {
    color: $brand-purple;
    margin: 0 0 4px 0;

    &:hover {
      color: $brand_soft_purple;
    }
  }

  .article-index-card-headline {
    margin: 4px 0 0;
  }

  .article-index-card-author {
    margin: 0 0 4px;

    &:hover {
      color: $brand_soft_purple;
    }
  }

  .article-index-card-description {
    color: $text_grey;
    line-height: 1.4;
    margin-top: 8px;

    @include respond-to(phone) {
      display: none;
    }
  }
}